<template>
  <v-flex>
    <v-layout row justify-center>
      <v-dialog v-model="dialog_edituser" persistent max-width="600px">
        <v-card>
          <v-card-title class="justify-center">
            <span class="headline">Edit User</span>
          </v-card-title>
          <v-card-text style="padding-top:0px;padding-bottom:0px">
            <v-container grid-list-sm style="padding-top:0px;padding-bottom:0px">
              <v-layout wrap>
                <v-flex xs12 sm6 md4>
                  <v-text-field class="popup-input" label="Name" v-model="Name" required></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field
                    class="popup-input"
                    label="UserName"
                    v-model="UserName"
                    persistent-hint
                    required
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-select
                    :items="findExistingUserGroups"
                    label="UserGroup"
                    v-model="UserGroup"
                    required
                  ></v-select>
                </v-flex>
                <v-flex xs6>
                  <v-text-field
                    class="popup-input"
                    label="Password*"
                    type="password"
                    v-model="Password"
                    required
                  ></v-text-field>
                </v-flex>

                <v-flex xs6>
                  <v-text-field
                    class="popup-input"
                    label="Confirm Password*"
                    type="password"
                    required
                    v-model="ConfirmPassword"
                  ></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
            <!-- <small>*indicates required field</small> -->
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="dialog_edituser = false">Close</v-btn>
            <v-btn color="blue darken-1" flat @click="editExistingUser();">Save</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
    <TableCard
      :tableColumns="tableColumns"
      :tableRows="tableRows"
      :filter="filter"
      @onSelectionChange="selectedUsers"
      @onEditSave="onEditSave"
      @onConfirmDelete="onConfirmDelete"
      :globalSearch="globalSearch"
      :ContentHeight="ContentHeight"
      deleteAlert="Delete User"
    ></TableCard>
  </v-flex>
</template>

<script>
import TableCard from "../../partials/TableCard/TableCard";
import $ from "jquery";
export default {
  name: "usermanagement",
  components: {
    TableCard
  },
  props: {
    globalSearch: {
      type: String,
      default: ""
    },
    userGroups: {
      type: Array,
      default: function() {
        return [
          {
            _id: "",
            UserGroupName: ""
          }
        ];
      }
    },
    userData: {
      type: Array,
      default: function() {
        return [];
      }
    }
  },
  data: function() {
    return {
      Name: "",
      _id: "",
      UserName: "",
      UserGroup: "",
      Password: "",
      ConfirmPassword: "",
      dialog_edituser: false,
      ContentHeight: "",
      selectedTableRows: [],
      filter: { Category: null, keyword: "" }
    };
  },
  mounted() {
    let height = $(window).height() - 208;
    this.ContentHeight = height.toString();
  },
  methods: {
    selectedUsers(item) {
      this.$emit("onUserSelection", item);
    },
    editExistingUser() {
      let newObj;
      let userGroupId;
      for (let i = 0; i < this.userGroups.length; i++) {
        if (this.UserGroup === this.userGroups[i].UserGroupName) {
          userGroupId = this.userGroups[i]._id;
        }
      }
      if (this.Password === "@123456@" && this.ConfirmPassword === "@123456@") {
        newObj = {
          UserId: this._id,
          Name: this.Name,
          UserName: this.UserName,
          UserGroup: userGroupId,
          Password: ""
        };
        this.$emit("onUpdateUser", newObj);
        this.dialog_edituser = false;
      } else {
        if (this.Password !== this.ConfirmPassword) {
          alert("Passwords do not match");
        } else {
          newObj = {
            UserId: this._id,
            Name: this.Name,
            UserName: this.UserName,
            UserGroup: userGroupId,
            Password: this.Password
          };
          this.$emit("onUpdateUser", newObj);
          this.dialog_edituser = false;
        }
      }
    },
    // selected: function(tableRow) {
    //   this.$emit("selectedTableRow", this.selectedTableRows);
    // },
    onConfirmDelete: function(tableRow) {
      this.$emit("onDeleteUser", tableRow);
    },
    // onRowClick: function(tableRow) {
    //   this.$emit("onRowClick", tableRow);
    // },
    onEditSave: function(item) {
      this.UserGroup = item.userGroupId;
      this.Name = item.name;
      this._id = item._id;
      this.Password = "@123456@";
      this.UserName = item.userName;
      this.ConfirmPassword = "@123456@";
      this.dialog_edituser = true;
    }
  },
  computed: {
    tableRows() {
      let userGroupId = "";
      let newTableRow = this.userData.map(row => {
        for (let i = 0; i < this.userGroups.length; i++) {
          if (this.userGroups[i]._id === row.UserGroup) {
            userGroupId = this.userGroups[i].UserGroupName;
          }
        }
        return {
          _id: row._id,
          name: row.Name,
          userName: row.UserName,
          userGroupId: userGroupId
        };
      });
      return newTableRow;
    },
    tableColumns() {
      return [
        {
          text: "Name",
          value: "name"
        },

        {
          text: "User Name",
          value: "userName"
        },
        {
          text: "User Group",
          value: "userGroupId"
        },
        {
          value: "_id",
          visible: false,
          key: true
        }
      ];
    },
    findExistingUserGroups() {
      let existingUserGroupsNames = [];
      let existingUserGroupsId = [];
      for (let i = 0; i < this.userGroups.length; i++) {
        existingUserGroupsNames.push(this.userGroups[i].UserGroupName);
        existingUserGroupsId.push(this.userGroups[i]._id);
      }
      this.existingUserGroups = {
        names: existingUserGroupsNames,
        values: existingUserGroupsId
      };
      return this.existingUserGroups.names;
    }
  }
};
</script>

<style scoped>
.titlebar {
  display: inline-flex;
  width: 100%;
  height: 100px;
  line-height: 20px;
}
.userbutton {
  margin-top: 25px;
  width: 100%;
}

.headline {
  font-weight: 700;
  color: #37454b;
}
</style>

