<template>
  <v-card class="elevation-3">
    <v-flex :style="myStyle">
      <v-layout align-space-around justify-space-around column fill-height>
        <v-dialog v-model="dialog" max-width="300px">
          <v-card>
            <v-card-title>
              <span class="headline">{{deleteAlert}}</span>
            </v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" flat @click="dialog = false">Cancel</v-btn>
              <v-btn color="blue darken-1" flat @click="confirmDeleteRow">Delete</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <!-- TABLE WITH CHECKBOX AND EDIT DELETE BUTTON-->
        <v-flex xs12 v-if="showCheckBox === true">
          <v-data-table
            v-model="selected"
            :headers="visibleColumns"
            :items="visibleRows"
            :hide-actions="true"
            item-key="_id"
            select-all
            :pagination.sync="pagination"
          >
            <template slot="items" slot-scope="props">
              <tr>
                <td :style="getStyle(props.item)">
                  <v-checkbox v-model="props.selected" primary hide-details></v-checkbox>
                </td>
                <td
                  v-for="column in visibleColumns"
                  :key="column.value"
                  @click="onRowClick(props.item)"
                  :style="getStyle(props.item)"
                >{{props.item[column.value]}}</td>
                <td class="justify-center layout px-0" :style="getStyle(props.item)">
                  <v-icon
                    v-if="showEditButton"
                    class="pr1"
                    small
                    @click="editItem(props.item)"
                    style="padding-top:14px"
                  >edit</v-icon>
                  <v-icon
                    v-if="showDeleteButton"
                    small
                    @click="deleteItem(props.item)"
                    style="padding-top:14px"
                  >delete</v-icon>
                </td>
              </tr>
            </template>
            <template slot="no-data">
              <v-alert :value="true" color="#fff" style="color:#000;border-color:#fff !important"></v-alert>
            </template>
          </v-data-table>
        </v-flex>
        <!-- TABLE WITHOUT CHECKBOX-->
        <v-flex xs12 v-if="!showCheckBox">
          <v-data-table
            v-model="selected"
            :headers="visibleColumns"
            :items="visibleRows"
            :hide-actions="true"
            item-key="_id"
            :pagination.sync="pagination"
          >
            <template slot="items" slot-scope="props">
              <tr class="rowClicked">
                <td
                  v-for="column in visibleColumns"
                  :key="column.value"
                  @click="onRowClick(props.item)"
                  :style="getStyle(props.item)"
                >{{props.item[column.value]}}</td>
                <td class="justify-center layout px-0" :style="getStyle(props.item)">
                  <v-icon
                    v-if="showEditButton"
                    small
                    @click="editItem(props.item)"
                    style="padding-top:14px"
                  >edit</v-icon>
                  <v-icon
                    v-if="showDeleteButton"
                    small
                    @click="deleteItem(props.item)"
                    style="padding-top:14px"
                  >delete</v-icon>
                </td>
              </tr>
            </template>
            <template slot="no-data">
              <v-alert :value="true" color="#fff" style="color:#000; border-color:#fff !important"></v-alert>
            </template>
          </v-data-table>
        </v-flex>
        <v-flex xs12>
          <div
            v-if="!hideRowsPerPageFooter"
            style="position:absolute;bottom:10px;margin-left:auto;margin-right:auto;width:100%;left:0;right: 0; "
          >
            <div style="postion:relative;" class="text-xs-center">
              <v-pagination total-visible="7" v-model="pagination.page" :length="pages"></v-pagination>
            </div>
          </div>
        </v-flex>
      </v-layout>
    </v-flex>
  </v-card>
</template>
<script>
import _ from "lodash";
import $ from "jquery";
export default {
  props: {
    highlightOnRowClick: {
      type: Boolean,
      default: false
    },
    deleteAlert: {
      type: String,
      default: "Delete Item"
    },
    hideRowsPerPageFooter: {
      type: Boolean,
      default: false
    },
    showCheckBox: {
      type: Boolean,
      default: true
    },

    showEditButton: {
      type: Boolean,
      default: true
    },
    showDeleteButton: {
      type: Boolean,
      default: true
    },
    tableColumns: {
      type: Array,
      default: function() {
        return [];
      }
    },
    tableRows: {
      type: Array,
      default: function() {
        return [];
      }
    },
    columnFilters: {
      type: Array,
      default: function() {
        return [];
      }
    },
    globalSearch: {
      type: String,
      default: ""
    },
    dateFilter: {
      type: Object,
      default: function() {
        return {};
      }
    },
    ContentHeight: {
      type: String,
      default: "300"
    }
  },
  mounted() {
    let self = this;

    $(".v-datatable__actions").each(function() {
      this.style.setProperty(
        "margin-top",
        self.localHeight + "px",
        "important"
      );
    });
    if (
      navigator.userAgent.match(/Android/i) ||
      navigator.userAgent.match(/webOS/i) ||
      navigator.userAgent.match(/iPhone/i) ||
      navigator.userAgent.match(/iPad/i) ||
      navigator.userAgent.match(/iPod/i) ||
      navigator.userAgent.match(/BlackBerry/i) ||
      navigator.userAgent.match(/Windows Phone/i)
    ) {
      this.isMobile = true;
    } else {
      this.isMobile = false;
    }
  },
  data: function() {
    return {
      deleteId: {},
      dialog: false,
      visibleRowsLength: 0,
      localHeight: "",
      COUNT: 0,
      isMobile: false,
      selectedRowId: "",
      style: "background:#fafafa",
      //PAGINATION INITIAL VALUE
      pagination: {
        rowsPerPage: 6,
        totalItems: 6
      },
      //EDIT DIALOG : CHANGED ROWS INITIAL VALUE
      //SELECTED CHECKBOX ITEMS
      selected: []
    };
  },
  computed: {
    myStyle() {
      return "height:" + this.localHeight + "px";
    },
    pages() {
      if (
        this.pagination.rowsPerPage == null ||
        this.pagination.totalItems == null
      )
        return 0;

      return Math.ceil(
        this.pagination.totalItems / this.pagination.rowsPerPage
      );
    },
    //COMPUTING VISIBLE COLUMNS
    visibleColumns() {
      //CHECK IF TABLE COLUMN EXISTS IN PROPS
      let vColumns = this.tableColumns;
      vColumns = vColumns.filter(vColumn => {
        if (typeof vColumn.visible === "undefined") return true;
        return vColumn.visible;
      });
      for (let i = 0; i < vColumns.length; i++) {
        if (typeof vColumns[i].sortable === "undefined") {
          vColumns[i].sortable = false;
        }
      }
      return vColumns;
    },
    //COMPUTING VISIBLE ROWS
    visibleRows() {
      if (typeof this.tableRows === "undefined") {
        return [];
      }
      let vRows;
      vRows = this.tableRows;
      //DATE FILTER VALIDATION
      let startDate;
      let newVrowsArr = [];
      let endDate;
      if (_.isEmpty(this.dateFilter) === true) {
        newVrowsArr = vRows;
      } else {
        startDate = this.dateFilter.startDate;
        endDate = this.dateFilter.endDate;
        for (let vRowsIndex = 0; vRowsIndex < vRows.length; vRowsIndex++) {
          if (
            startDate <= vRows[vRowsIndex].Date &&
            endDate >= vRows[vRowsIndex].Date
          ) {
            newVrowsArr.push(vRows[vRowsIndex]);
          }
        }
      }
      //Applying date filter
      vRows = newVrowsArr;
      //GLOBAL SEARCH VALIDATION
      let someArr = [];
      let globalArr = [];
      if (vRows)
        for (let p = 0; p < vRows.length; p++) {
          someArr.push(Object.values(vRows[p]));
        }
      for (let q = 0; q < someArr.length; q++) {
        for (let l = 0; l < someArr[q].length; l++) {
          if (
            someArr[q][l]
              .toString()
              .toLowerCase()
              .search(this.globalSearch.toLowerCase()) !== -1
          ) {
            globalArr.push(vRows[q]);
            break;
          }
        }
      }
      // let globalSearch;
      // let globalArr;
      // let isGValid = true;
      // if (this.globalSearch === "") isGValid = false;
      // if (isGValid === false) {
      //   globalArr = vRows;
      // } else {
      //   globalSearch = this.globalSearch;
      //   globalArr = [];

      //   //GLOBAL SEARCH
      //   for (let vRows1Index = 0; vRows1Index < vRows.length; vRows1Index++) {
      //     if (_.includes(vRows[vRows1Index], globalSearch)) {
      //       globalArr.push(vRows[vRows1Index]);
      //     }
      //   }
      // }
      vRows = globalArr;

      //COLUMN FILTER
      let res;
      let isCFValid = true;
      let columnFilters;
      let result = [];
      if (this.columnFilters.length === 0) isCFValid = false;
      if (isCFValid === true) {
        columnFilters = this.columnFilters;
        for (
          let columnFiltersIndex = 0;
          columnFiltersIndex < columnFilters.length;
          columnFiltersIndex++
        ) {
          result[columnFiltersIndex] = _.filter(
            vRows,
            columnFilters[columnFiltersIndex]
          );
        }
        if (result.length - 1 === 0) {
          res = result[0];
        } else {
          for (
            let resultIndex = 0;
            resultIndex < result.length - 1;
            resultIndex++
          ) {
            res = _.intersection(result[resultIndex], result[resultIndex + 1]);
          }
        }
        vRows = res;
      }
      return vRows;
    }
  },
  watch: {
    //TO DEFINE CONTENT HEIGHT
    ContentHeight: function(newWindowHeight) {
      this.localHeight = newWindowHeight;
      if (this.isMobile === false) {
        this.pagination.rowsPerPage =
          Math.round(Number(newWindowHeight) / 58) - 1;
        // console.log(this.pagination.rowsPerPage);
        // console.log(this.visibleRowsLength);
        //   if (this.visibleRowsLength < this.pagination.rowsPerPage) {
        //     let val = 1;
        //     if (this.visibleRowsLength === 0) {
        //       val = this.pagination.rowsPerPage - 1;
        //     } else {
        //       val = this.pagination.rowsPerPage - this.visibleRowsLength;
        //     }
        //     let height = val * 7;

        //     $(".v-datatable__actions").each(function() {
        //       this.style.setProperty("margin-top", height + "vh", "important");
        //     });
        //   }
      } else {
        this.pagination.rowsPerPage = 5;
      }
    }
  },
  //TO FIND SELECTED ITEMS
  updated: function() {
    if (this.hideRowsPerPageFooter === true) {
      this.rowsPerPage = 999;
    }
    this.pagination.totalItems = this.visibleRows.length;
    let array;
    if (this.selected.length === 0) {
      array = [];
    } else if (this.selected.length > 0) {
      for (let i = 0; i < this.selected.length; i++) {
        array = _.map(this.selected, function(o) {
          return o._id;
        });
      }
    }
    this.$emit("onSelectionChange", array);
  },
  created() {
    this.initialize();
    this.pagination.rowsPerPage = 10;
  },
  methods: {
    getStyle(data) {
      if (this.highlightOnRowClick) {
        if (data._id == this.selectedRowId) {
          return "background:#1976d2";
        } else {
          return "background:#fafafa";
        }
      }
    },
    getCount() {
      this.COUNT = this.COUNT + 1;
      return this.COUNT;
    },
    //INITIALIZE TABLE ROW
    initialize() {
      this.tableRows;
    },
    //EDIT ITEMS
    editItem(item) {
      //old Item
      this.$emit("onEditSave", item);
    },
    //DELETE ITEM
    deleteItem(item) {
      let obj = {
        _id: item._id
      };
      this.dialog = true;
      //Delete Item
      this.deleteId = obj;
    },
    confirmDeleteRow() {
      this.$emit("onConfirmDelete", this.deleteId);
      this.dialog = false;
    },

    //ROW CLICK VALUE
    onRowClick(item) {
      //$("tr.rowClicked").toggleClass("bounce");
      this.selectedRowId = item._id;
      //ON ROW CLICK
      let obj = {
        _id: item._id
      };
      this.$emit("onRowClick", obj);
    }
  }
};
</script>
<style >
/* TO FIX HEADER BORDER PROBLEM */
.theme--light.v-table tbody tr:first-child {
  border-bottom: 1px solid rgba(53, 49, 49, 0.12);
  border-top: 1px solid grey;
}
/* TO FIX HEADER BORDER PROBLEM */
.theme--light.v-table thead tr:first-child {
  border-bottom: 0px;
}
.elevation-1 {
  background: #fff;
}
tbody {
  cursor: pointer;
}
.bounce {
  background: #212;
}
</style>


 