import Service from "./Service";
const url = "api/user/";

class UserService extends Service {
  constructor(component) {
    super(component, url);
  }

  fetch() {
    return new Promise(async (resolve, reject) => {
      try {
        let { Users } = await this.getFromServer("fetch");
        resolve(Users);
      } catch (cause) {
        reject(cause);
      }
    });
  }

  create(UserGroup, Name, UserName, Password) {
    return new Promise(async (resolve, reject) => {
      try {
        await this.postToServer("create", {
          UserGroup,
          Name,
          UserName,
          Password
        });
        resolve("created");
      } catch (cause) {
        reject(cause);
      }
    });
  }

  read() {
    return new Promise(async (resolve, reject) => {
      try {
        let { User } = await this.getFromServer("read");
        resolve(User);
      } catch (cause) {
        reject(cause);
      }
    });
  }

  update(UserId, UserParameters, UserValues) {
    return new Promise(async (resolve, reject) => {
      try {
        //console.log(UserId, UserParamters, UserValues)
        await this.postToServer("update", {
          UserId,
          UserParameters,
          UserValues
        });
        resolve("updated");
      } catch (cause) {
        reject(cause);
      }
    });
  }

  delete(UserId) {
    return new Promise(async (resolve, reject) => {
      try {
        await this.postToServer("delete", {
          UserId
        });
        resolve("deleted");
      } catch (cause) {
        reject(cause);
      }
    });
  }

  login(UserName, Password) {
    return new Promise(async (resolve, reject) => {
      try {
        let res = await this.postToServer("login", {
          UserName,
          Password
        });
        let user = await this.getFromServer("read");
        this.component.$session.set("KEY", user._id);
        resolve(res);
      } catch (cause) {
        reject(cause);
      }
    });
  }

  logout() {
    return new Promise(async (resolve, reject) => {
      try {
        await this.postToServer("logout");
        resolve("done");
      } catch (cause) {
        reject(cause);
      }
    });
  }

  export(UserIds) {
    return new Promise(async (resolve, reject) => {
      try {
        await this.fileDownloadFromServer(
          "export",
          "UserIds",
          UserIds,
          "user_qrcodes.zip"
        );
        resolve("done");
      } catch (cause) {
        reject(cause);
      }
    });
  }
}

export default UserService;
