<template>
  <v-flex row wrap>
    <v-layout row justify-center>
      <v-dialog v-model="dialog_adduser" persistent max-width="600px">
        <v-card>
          <v-card-title class="justify-center">
            <span class="headline">Creating User</span>
          </v-card-title>
          <v-card-text style="padding-top:0px;padding-bottom:0px">
            <v-container grid-list-sm style="padding-top:0px;padding-bottom:0px">
              <v-layout wrap>
                <v-flex xs12 sm6 md4>
                  <v-text-field class="popup-input" label="Name" v-model="Name" required></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field
                    class="popup-input"
                    label="UserName"
                    v-model="UserName"
                    persistent-hint
                    required
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-select
                    :items="findExistingUserGroups"
                    label="UserGroup"
                    v-model="UserGroup"
                    required
                  ></v-select>
                </v-flex>
                <v-flex xs6>
                  <v-text-field
                    class="popup-input"
                    label="Password*"
                    type="password"
                    v-model="Password"
                    required
                  ></v-text-field>
                </v-flex>

                <v-flex xs6>
                  <v-text-field
                    class="popup-input"
                    label="Confirm Password*"
                    type="password"
                    required
                    v-model="ConfirmPassword"
                  ></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
            <!-- <small>*indicates required field</small> -->
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="dialog_adduser = false">Close</v-btn>
            <v-btn color="blue darken-1" flat @click="addNewUser();">Add User</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-dialog v-model="dialog_addusergroup" persistent max-width="600px">
        <v-card>
          <v-card-title class="justify-center">
            <span class="headline">Creating UserGroup</span>
          </v-card-title>
          <v-card-text style="padding:0px 15px">
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12>
                  <v-text-field label="User Group" v-model="AddUserGroup" required></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
            <!-- <small>*indicates required field</small> -->
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="dialog_addusergroup = false">Close</v-btn>
            <v-btn
              color="blue darken-1"
              flat
              @click="addNewUserGroup(); dialog_addusergroup = false"
            >Add Usergroup</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-dialog v-model="dialog_deleteusergroup" persistent max-width="600px">
        <v-card>
          <v-card-title class="justify-center">
            <span class="headline">Delete UserGroup</span>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12>
                  <v-select
                    :items="findExistingUserGroups"
                    label="Select a user group"
                    v-model="ExistingUserGroupSelected"
                    required
                  ></v-select>
                </v-flex>
              </v-layout>
            </v-container>
            <!-- <small>*indicates required field</small> -->
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="dialog_deleteusergroup = false">Close</v-btn>
            <v-btn
              color="blue darken-1"
              flat
              @click=" deleteUserGroups(); dialog_deleteusergroup = false"
            >Delete Usergroup</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
    <v-layout row wrap>
      <v-flex pa-2 xs12 sm4 class="text-sm-left text-xs-center">
        <h1 style="font-size:40px">User Management</h1>
      </v-flex>
      <v-flex xs6 sm2 lg1>
        <v-btn v-if="selectedUsersValue.length !==0" dark block large @click="EportUsers">Export</v-btn>
      </v-flex>
      <v-flex sm2 xs6 lg1 class="text-sm-right text-xs-left" pr-3>
        <v-btn @click="dialog_adduser = true" dark style="height:45px;width:100%">Add User</v-btn>
      </v-flex>
      <v-flex sm4 xs12 pa-1 lg2 class="text-sm-right text-xs-left" pr-2>
        <!-- v-model="search"-->
        <v-text-field
          append-icon="search"
          label="Search"
          v-model="searchVar"
          @keyup="searchVal()"
          single-line
          hide-details
          solo
        ></v-text-field>
      </v-flex>
      <v-layout row wrap pr-2>
        <v-flex xs5 sm6 class="text-sm-right text-xs-left" pr-2>
          <v-btn
            @click="dialog_addusergroup = true"
            dark
            style="height:45px;width: 100%;"
          >Add Usergroup</v-btn>
        </v-flex>
        <v-flex xs7 sm6 class="text-sm-left text-xs-right" pr-2>
          <v-btn
            @click="dialog_deleteusergroup = true"
            dark
            style="height:45px;width: 100%;
"
          >Delete Usergroup</v-btn>
        </v-flex>
      </v-layout>
    </v-layout>
  </v-flex>
</template>

<script>
import _ from "lodash";
export default {
  name: "UsertitleBar",
  components: {},
  props: {
    selectedUsersValue: {
      type: Array,
      default: function() {
        return [];
      }
    },
    userGroups: {
      type: Array,
      default: function() {
        return [];
      }
    }
  },
  data() {
    return {
      ExistingUserGroupSelected: "",
      existingUserGroups: {},
      AddUserGroup: "",
      UserName: "",
      UserGroup: "",
      Name: "",
      Password: "",
      ConfirmPassword: "",
      searchVar: "",
      dialog_adduser: false,
      dialog_addusergroup: false,
      dialog_deleteusergroup: false,
      dialog_update_user: false,
      defaultSearch: "All",
      pagination: {
        sortBy: "name"
      }
    };
  },
  mounted() {
    // let existingUsersGroupsObj = {},
  },
  computed: {
    findExistingUserGroups() {
      let existingUserGroupsNames = [];
      let existingUserGroupsId = [];
      for (let i = 0; i < this.userGroups.length; i++) {
        existingUserGroupsNames.push(this.userGroups[i].UserGroupName);
        existingUserGroupsId.push(this.userGroups[i]._id);
      }

      this.existingUserGroups = {
        names: existingUserGroupsNames,
        values: existingUserGroupsId
      };
      return this.existingUserGroups.names;
    }
  },
  methods: {
    EportUsers() {
      this.$emit("ExportUsers");
    },
    searchVal() {
      this.$emit("onSearchValue", this.searchVar);
    },
    addNewUser() {
      let NewUser;
      let userGroupId;
      for (let i = 0; i < this.userGroups.length; i++) {
        if (this.UserGroup === this.userGroups[i].UserGroupName) {
          userGroupId = this.userGroups[i]._id;
        }
      }
      if (
        this.Password === "" ||
        this.ConfirmPassword === "" ||
        this.Name === "" ||
        this.UserName === "" ||
        this.Password === "" ||
        this.UserGroup === ""
      ) {
        alert("Please enter all details");
      } else {
        if (this.Password !== this.ConfirmPassword) {
          alert("Passwords do not match!");
        } else {
          NewUser = {
            Name: this.Name,
            UserName: this.UserName,
            UserGroup: userGroupId,
            Password: this.Password
          };
        }
      }
      if (typeof NewUser !== "undefined") {
        this.dialog_adduser = false;
        this.$emit("onAddUser", NewUser);
      }
    },
    addNewUserGroup() {
      let obj = {
        UserGroupName: this.AddUserGroup
      };
      this.$emit("onAddUsergroup", obj);
    },
    deleteUserGroups() {
      let existingUserGroupsId = 0;
      for (let i = 0; i < this.existingUserGroups.names.length; i++) {
        if (
          this.ExistingUserGroupSelected === this.existingUserGroups.names[i]
        ) {
          let obj = { UserGroupId: this.existingUserGroups.values[i] };
          this.$emit("onDeleteUserGroup", obj);
        }
      }
    }
  }
};
</script>

