import PageService from "../services/pageService";

const pageModule = {
    state: {
        pages: []
    },

    getters: {
        getPages: state => {
            return state.pages;
        },
    },

    mutations: {
        fetchPages: (state, pages) => {
            state.pages = pages;
        }
    },

    actions: {
        fetchPages: async (context, payload) => {
            let {
                component
            } = payload;
            let pageService = new PageService(component);
            let pages = await pageService.fetch();
            context.commit("fetchPages", pages);
        },
        SOCKET_PAGE_CREATE: async context => {
            let pageService = new PageService();
            let pages = await pageService.fetch();
            context.commit("fetchPages", pages);
        },
        SOCKET_PAGE_REMOVE: async context => {
            let pageService = new PageService();
            let pages = await pageService.fetch();
            context.commit("fetchPages", pages);
        }
    }
};

export default pageModule;