<template>
  <v-app id="app">
    <notifications group="foo"/>
    <v-content ref="content">
      <NavBar
        ref="navbar"
        v-if="!amIOnLoginPage"
        product="Augmented Reality"
        :menu="menu"
        :UserName="$session.get('USER_NAME')"
        :UserGroup="$session.get('USER_GROUP_NAME')"
        :Name="$session.get('NAME')"
        @onLogout="handleLogout"
      ></NavBar>
      <vue-progress-bar></vue-progress-bar>
      <router-view :style="defaultMargin()"></router-view>
    </v-content>
  </v-app>
</template>
<script>
//TESTING
import NavBar from "./components/partials/NavBar/NavBar.vue";
export default {
  name: "App",

  data() {
    return {
      isUserLoggedIn: false,
      amIOnLoginPage: false,
      menu: [
        {
          _id: "1",
          title: "User Management",
          icon: "supervised_user_circle",
          path: "/userManagement"
        }
      ]
    };
  },
  mounted() {
    this.updateUIParamters();
    this.amIOnLoginPage =
      this.$router.currentRoute.path == "/login" ||
      this.$router.currentRoute.path == "/"
        ? true
        : false;
  },
  async created() {
    try {
      await this.$store.dispatch("read", {
        component: this
      });
    } catch (cause) {
      this.$log.debug("sessionFailed", cause);
    }
  },
  components: {
    NavBar
  },
  watch: {
    $route(to) {
      this.amIOnLoginPage = to.path == "/login" ? true : false;
    }
  },
  methods: {
    defaultMargin() {
      if (!this.amIOnLoginPage) {
        return "margin-top:60px;";
      } else return "margin-top:0px;";
    },
    handleLogout() {
      this.$store.dispatch("logout", {
        component: this
      });
    },
    async handleUserManagement() {
      try {
        this.$router.push("/userManagement");
      } catch (cause) {
        this.$log.debug("loginFailed", cause);
      }
    },

    updateUIParamters() {
      this.$store.commit(
        "setWebPageHeight",
        this.$refs.content.$el.clientHeight
      );
      let phoneAgents = [
        /Android/i,
        /webOS/i,
        /iPhone/i,
        /iPad/i,
        /iPod/i,
        /BlackBerry/i,
        /Windows Phone/i
      ];
      let res = _.find(phoneAgents, phoneAgent => {
        return navigator.userAgent.match(phoneAgent);
      });
      this.$store.commit("setIsPhone", typeof res !== "undefined");
    }
  }
};
</script>

<style>
html {
  overflow-y: auto;
}
</style>

