import UserService from "../services/userService";

function setSessionKeys(component, user) {
  component.$session.set("KEY", user._id);
  component.$session.set("USER_NAME", user.UserName);
  component.$session.set("NAME", user.Name);
  component.$session.set("USER_GROUP_NAME", user.UserGroup.UserGroupName);
}

function startSession(component, user) {
  if (component.$session.exists()) {
    setSessionKeys(component, user);
    return;
  }
  component.$session.start();
  setSessionKeys(component, user);
  component.$router.push("/jobs");
}

function stopSession(component) {
  component.$session.destroy();
  component.$router.push("/login");
}

const userModule = {
  state: {
    users: [],
    user: {}
  },

  getters: {
    getUsers: state => {
      return state.users;
    },
    getUserId: state => {
      return state.user._id;
    },

    userName: state => {
      return state.user.UserName;
    }
  },

  mutations: {
    fetchUsers: (state, users) => {
      state.users = users;
    }
  },

  actions: {
    fetchUsers: async (context, payload) => {
      let {
        component
      } = payload;
      context.commit("fetchUsers", []);
      let userService = new UserService(component);
      let users = await userService.fetch();
      context.commit("fetchUsers", users);
    },
    createUser: async (context, payload) => {
      let {
        component,
        UserGroup,
        Name,
        UserName,
        Password
      } = payload;
      let userService = new UserService(component);
      await userService.create(UserGroup, Name, UserName, Password);
      let users = await userService.fetch();
      context.commit("fetchUsers", users);
    },

    login: async (context, payload) => {
      let {
        component,
        UserName,
        Password
      } = payload;
      let userService = new UserService(component);

      await userService.login(UserName, Password);
      let user = await userService.read();
      startSession(component, user);
    },
    logout: async (context, payload) => {
      let {
        component
      } = payload;
      let userService = new UserService(component);

      await userService.logout();

      stopSession(component);

    },
    read: async (context, payload) => {
      let {
        component
      } = payload;
      try {
        let userService = new UserService(component);
        let user = await userService.read();
        startSession(component, user);
      } catch (cause) {
        stopSession(component);
      }
    },
    deleteUser: async (context, payload) => {
      let {
        component,
        UserId
      } = payload;
      let userService = new UserService(component);
      await userService.delete(UserId);
      let users = await userService.fetch();
      context.commit("fetchUsers", users);
    },
    updateUser: async (context, payload) => {
      let {
        component,
        UserId,
        UserParamters,
        UserValues
      } = payload;
      let userService = new UserService(component);
      await userService.update(UserId, UserParamters, UserValues);
      let users = await userService.fetch();
      context.commit("fetchUsers", users);
    },
    exportUser: async (context, payload) => {
      let {
        component,
        UserIds
      } = payload;
      let userService = new UserService(component);
      await userService.export(UserIds);
    },
    SOCKET_USER_CREATE: async context => {
      let userService = new UserService();
      let users = await userService.fetch();
      context.commit("fetchUsers", users);
    },
    SOCKET_USER_REMOVE: async context => {
      let userService = new UserService();
      let users = await userService.fetch();
      context.commit("fetchUsers", users);
    }
  }
};

export default userModule;