<template>
  <v-layout row wrap>
    <v-flex pb-2>
      <v-toolbar dark>
        <v-toolbar-title>User Access Rights</v-toolbar-title>
      </v-toolbar>
      <v-flex>
        <v-card :style="formHeight">
          <v-flex xs12 pl-2 pr-2 pt-2>
            <h2>User Group</h2>
            <v-flex>
              <v-select
                :items="dropDownItems"
                v-model="chosenUserGroup"
                @input="handleUserGroupSelectionChange"
                solo
                label="Select a user group"
              ></v-select>
            </v-flex>
            <v-flex xs12 pl-2 pr-2 pt-2 v-if="this.selectedUserGroup !=null">
              <h2>Assigned Pages</h2>
              <v-flex v-for="(item,i) in Pages" :key="i">
                <v-checkbox
                  v-model="AssignedUG[i]"
                  :label="item.PageName"
                  @change="handleChangeUserGroup(item, AssignedUG[i])"
                ></v-checkbox>
              </v-flex>
            </v-flex>
          </v-flex>
        </v-card>
      </v-flex>
    </v-flex>
  </v-layout>
</template>
<script>
import _ from "lodash";
import $ from "jquery";
export default {
  props: {
    Pages: {
      type: Array,
      default: function() {
        return [];
      }
    },
    userGroups: {
      type: Array,
      default: function() {
        return [];
      }
    }
  },
  watch: {
    userGroups(newValue) {
      if (this.selectedUserGroup !== null) {
        this.handleUserGroupSelectionChange();
      }
    }
  },
  data() {
    return {
      chosenUserGroup: "",
      AssignedUG: [],
      selectedUserGroup: null,
      checkbox: [],
      formHeight: ""
    };
  },
  computed: {
    dropDownItems() {
      let dropDownArr = [];
      for (let i = 0; i < this.userGroups.length; i++) {
        dropDownArr.push(this.userGroups[i].UserGroupName);
      }
      return dropDownArr;
    }
  },
  mounted() {
    for (let i = 0; i < this.Pages.length; i++) {
      this.AssignedUG.push(false);
    }
    let height = $(window).height() - 272;
    this.formHeight = "height:" + height.toString() + "px;overflow-y:auto";
  },
  methods: {
    displayPages() {
      this.AssignedUG = [];
      for (let k = 0; k < this.Pages.length; k++) {
        this.AssignedUG.push(false);
      }
      for (let i = 0; i < this.selectedUserGroup.Pages.length; i++) {
        for (let j = 0; j < this.Pages.length; j++) {
          if (this.Pages[j]._id === this.selectedUserGroup.Pages[i]) {
            this.AssignedUG[j] = true;
          }
        }
      }
    },
    handleChangeUserGroup(Page, AssignedUG) {
      let obj = (obj = {
        UserGroupId: this.selectedUserGroup._id,
        PageId: Page._id
      });
      if (AssignedUG) {
        this.$emit("onSetPages", obj);
        //
      } else if (!AssignedUG) {
        this.$emit("onUnsetPages", obj);
      }
    },
    handleUserGroupSelectionChange() {
      this.selectedUserGroup = _.find(this.userGroups, userGroup => {
        return userGroup.UserGroupName == this.chosenUserGroup;
      });
      this.displayPages();
    }
  }
};
</script>