<template>
  <v-container fluid>
    <v-layout row wrap>
      <v-flex xs12 pa-1>
        <UserTitleBar
          @ExportUsers="handleExportUsers"
          :userGroups="userGroups"
          @onAddUser="handleAddUser"
          :selectedUsersValue="selectedUsersValue"
          @onAddUsergroup="handleAddUserGroup"
          @onDeleteUserGroup="handleDeleteUserGroup"
          @onSearchValue="searchValue"
        ></UserTitleBar>
      </v-flex>
      <v-flex xs12 sm9 pa-1>
        <UserManagementTable
          @onUserSelection="selectedUser"
          :userData="users"
          :userGroups="userGroups"
          :globalSearch="globalSearch"
          @onDeleteUser="handleDeleteUser"
          @onUpdateUser="handleEditSave"
        />
      </v-flex>
      <v-flex xs12 sm3 pa-1>
        <UserManagementAccess
          :Pages="Pages"
          :userGroups="userGroups"
          @onSetPages="handleAddPage"
          @onUnsetPages="handleRemovePage"
        />
      </v-flex>
    </v-layout>
  </v-container>
</template>
<script>
import axios from "axios";
import UserTitleBar from "./UserManagementTitleBar";
import UserManagementTable from "./UserManagementTable";
import UserManagementAccess from "./UserManagementAccess";
export default {
  components: {
    UserTitleBar,
    UserManagementAccess,
    UserManagementTable
  },
  computed: {
    users() {
      return this.$store.getters.getUsers;
    },
    userGroups() {
      return this.$store.getters.getUserGroups;
    },
    Pages() {
      return this.$store.getters.getPages;
    }
  },
  created() {
    this.$store.dispatch("fetchUserGroups", { component: this });
    this.$store.dispatch("fetchUsers", { component: this });
    this.$store.dispatch("fetchPages", { component: this });
  },
  data() {
    return {
      selectedUsersValue: [],
      globalSearch: ""
    };
  },

  methods: {
    selectedUser(item) {
      this.selectedUsersValue = item;
    },
    handleAddUser(item) {
      let { Name, UserName, UserGroup, Password } = item;
      this.$store.dispatch("createUser", {
        component: this,
        Name,
        UserName,
        UserGroup,
        Password
      });
    },

    handleAddUserGroup(item) {
      let { UserGroupName } = item;
      this.$store.dispatch("createUserGroup", {
        component: this,
        UserGroupName
      });
    },

    handleDeleteUserGroup(item) {
      let { UserGroupId } = item;
      this.$store.dispatch("deleteUserGroup", {
        component: this,
        UserGroupId
      });
    },
    searchValue(item) {
      this.globalSearch = item;
    },
    handleDeleteUser(items) {
      let { _id } = items;

      this.$store.dispatch("deleteUser", {
        component: this,
        UserId: _id
      });
    },
    handleEditSave(items) {
      let { UserId, Name, UserName, UserGroup, Password } = items;
      let UserParamters = "Name,UserName,UserGroup";
      let UserValues = Name + "," + UserName + "," + UserGroup;

      if (Password.trim().length > 0) {
        UserParamters += ",Password";
        UserValues += "," + Password;
      }

      this.$store.dispatch("updateUser", {
        component: this,
        UserId,
        UserParamters,
        UserValues
      });
    },
    handleExportUsers() {
      let UserIdsStr = "";
      this.selectedUsersValue.forEach(userId => {
        UserIdsStr += userId;
        UserIdsStr += ",";
      });

      UserIdsStr = UserIdsStr.substring(0, UserIdsStr.length - 1);
      this.$store.dispatch("exportUser", {
        component: this,
        UserIds: UserIdsStr
      });
    },
    //API
    handleRemovePage(item) {
      let { UserGroupId, PageId } = item;
      this.$store.dispatch("removePage", {
        component: this,
        UserGroupId,
        PageId
      });
    },
    //API
    handleAddPage(item) {
      let { UserGroupId, PageId } = item;
      this.$store.dispatch("addPage", { component: this, UserGroupId, PageId });
    }
  }
};
</script>