import Vue from "vue";
import VueLogger from 'vuejs-logger';

const vueLoggerConfig = {
    isEnabled: true,
    logLevel: 'error',
    stringifyArguments: true,
    showLogLevel: true,
    showMethodName: false,
    separator: '|',
    showConsoleColors: true

}

Vue.use(VueLogger, vueLoggerConfig);