  <template>
  <v-container fluid fill-height style="background:#fff">
    <v-layout align-center justify-center>
      <v-flex xs12 sm8 md4>
        <v-card light class="elevation-12">
          <v-toolbar dark>
            <v-toolbar-title style="font-size:25px">
              Origa
              <strong>Augmented Reality</strong>
            </v-toolbar-title>
            <v-spacer></v-spacer>
            <img src="../../../assets/origa-ar.jpeg" alt="origaAR" style="width:30px;height:30px;">
          </v-toolbar>
          <v-card-text>
            <v-form style="color:black">
              <v-text-field
                prepend-icon="person"
                name="message"
                label="Login"
                type="text"
                required
                autofocus
                v-model="UserName"
              ></v-text-field>
              <v-text-field
                id="password"
                prepend-icon="lock"
                name="password"
                required
                label="Password"
                type="password"
                v-model="Password"
              ></v-text-field>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="#212121" @click.prevent="login" style="color:#fff" type="submit">Login</v-btn>
              </v-card-actions>
            </v-form>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  name: "Login",
  components: {},

  data() {
    return {
      UserName: "",
      Password: ""
    };
  },
  beforeCreate() {
    if (this.$session.exists()) {
      this.$router.push("/jobs");
    }
  },

  methods: {
    async login() {
      await this.$store.dispatch("login", {
        component: this,
        UserName: this.UserName,
        Password: this.Password
      });
      this.$router.push("/jobs");
    }
  }
};
</script>
<style scoped>
.primary--text {
  color: #000 !important;
  /* carte-color: #000 !important; */
}
</style>