import Service from "./Service";
const url = "api/userGroup/";

class UserGroupService extends Service {
    constructor(component) {
        super(component, url);
    }

    fetch() {
        return new Promise(async (resolve, reject) => {
            try {
                let {
                    UserGroups
                } = await this.getFromServer("fetch")
                resolve(UserGroups);
            } catch (cause) {
                reject(cause);
            }
        });
    }

    create(UserGroupName) {
        return new Promise(async (resolve, reject) => {
            try {
                await this.postToServer("create", {
                    UserGroupName
                })
                resolve("created");
            } catch (cause) {
                reject(cause);
            }
        });
    }

    delete(UserGroupId) {
        return new Promise(async (resolve, reject) => {
            try {
                await this.postToServer("delete", {
                    UserGroupId
                })
                resolve("deleted");
            } catch (cause) {
                reject(cause);
            }
        });
    }

    addPage(UserGroupId, PageId) {
        return new Promise(async (resolve, reject) => {
            try {
                await this.postToServer("addPage", {
                    UserGroupId,
                    PageId
                })
                resolve("updated");
            } catch (cause) {
                reject(cause);
            }
        });
    }

    removePage(UserGroupId, PageId) {
        return new Promise(async (resolve, reject) => {
            try {
                await this.postToServer("removePage", {
                    UserGroupId,
                    PageId
                })
                resolve("updated");
            } catch (cause) {
                reject(cause);
            }
        });
    }
}



export default UserGroupService