const userGroupModule = {
    state: {
        webPageHeight: 0,
        navbarHeight: 64,
        isPhone: false
    },

    getters: {
        getWebPageHeight: state => {
            return state.webPageHeight;
        },
        getNavbarHeight: state => {
            return state.navbarHeight;
        },
        getIsPhone: state => {
            return state.isPhone;
        }
    },

    mutations: {
        setWebPageHeight: (state, webPageHeight) => {
            state.webPageHeight = webPageHeight;
        },
        setNavbarHeight: (state, navbarHeight) => {
            state.navbarHeight = navbarHeight;
        },
        setIsPhone: (state, isPhone) => {
            state.isPhone = isPhone;
        }
    },

}

export default userGroupModule;