<template>
  <div>
    <v-toolbar fixed>
      <v-menu offset-y bottom right open-on-hover class="hidden-md-and-up">
        <v-toolbar-side-icon slot="activator" style="top:6px"></v-toolbar-side-icon>
        <v-list dense mt-4>
          <v-list-tile
            v-for="item in menu"
            :to="item.path"
            :key="item._id"
            @click="menuItemClick(item._id)"
          >
            <v-list-tile-content>
              <v-list-tile-title style="text-align:left;font-size:16px;text-transform:uppercase">
                {{ item.title }}
                <v-icon style="padding-left:10px">{{item.icon}}</v-icon>
              </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
      </v-menu>
      <v-toolbar-title>
        <span style="margin-right:10px">{{company}}</span>
        <strong>{{product}}</strong>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items v-for="item in menu" :key="item._id" class="hidden-sm-and-down">
        <v-btn :to="item.path" flat @click="menuItemClick(item._id)">
          {{ item.title }}
          <v-icon style="padding-left:10px">{{item.icon}}</v-icon>
        </v-btn>
      </v-toolbar-items>
      <v-toolbar-items>
        <v-menu offset-y open-on-hover bottom left>
          <v-btn
            slot="activator"
            style="background-color:#fff;border:0;box-shadow:none"
          >{{UserName}}</v-btn>
          <v-list>
            <v-list-tile>
              <v-list-tile-title style="text-align:left;font-size:16px;">{{Name}}({{UserGroup}})</v-list-tile-title>
            </v-list-tile>
            <v-list-tile @click="Logout(true)">
              <v-list-tile-title
                style="text-align:left;font-size:16px;text-transform:uppercase"
              >Logout
                <v-icon style="padding-left:10px">power_settings_new</v-icon>
              </v-list-tile-title>
            </v-list-tile>
          </v-list>
        </v-menu>
      </v-toolbar-items>
    </v-toolbar>
  </div>
</template>
 
<script>
export default {
  name: "NavBar",
  //   props: ["company", "product", "UserName", "menu", "UserGroup"],
  props: {
    company: {
      type: String,
      default: "Origa"
    },
    product: {
      type: String,
      default: "Augmented Reality"
    },
    UserName: {
      type: String,
      default: "Piyushiii"
    },
    Name: {
      type: String,
      default: "Piyush A"
    },

    UserGroup: {
      type: String,
      default: "Admin"
    },
    menu: {
      type: Array,
      default() {
        return [
          {
            title: "Users",
            icon: "supervised_user_circle",
            _id: "1",
            path: "/Users"
          },
          { title: "Rolls", icon: "all_inbox", _id: "2", path: "/Rolls" }
        ];
      }
    }
  },
  data() {
    return {
      hamburgerdrawer: true,
      mini: true,
      right: null,
      userdrawer: null,
      showLogoutScreen: false,
      isHidden: false
    };
  },
  methods: {
    menuItemClick(item) {
      let obj = {
        _id: item
      };
      this.$emit("onMenuItemClick", obj);
    },
    Logout(val) {
      if (val === true) {
        this.$emit("onLogout");
      }
    }
  }
};
</script>

<style scoped>
.theme--light.v-toolbar {
  background: #fff;
}
.v-toolbar__content,
.v-toolbar__extension {
  padding: 0 !important;
}
</style>
