import Service from "./Service";
const url = "api/page/";

class PageService extends Service {
    constructor(component) {
        super(component, url);
    }

    fetch() {
        return new Promise(async (resolve, reject) => {
            try {
                let {
                    Pages
                } = await this.getFromServer("fetch")
                resolve(Pages);
            } catch (cause) {
                reject(cause);
            }
        });
    }


}



export default PageService