import UserGroupService from "../services/userGroupService";

const userGroupModule = {
  state: {
    userGroups: []
  },

  getters: {
    getUserGroups: state => {
      return state.userGroups;
    },
  },

  mutations: {
    fetchUserGroups: (state, userGroups) => {
      state.userGroups = userGroups;
    }
  },

  actions: {
    fetchUserGroups: async (context, payload) => {
      let {
        component
      } = payload;
      let userGroupService = new UserGroupService(component);
      let userGroups = await userGroupService.fetch();
      context.commit("fetchUserGroups", userGroups);
    },
    createUserGroup: async (context, payload) => {
      let {
        component,
        UserGroupName
      } = payload;
      let userGroupService = new UserGroupService(component);
      await userGroupService.create(UserGroupName);
      let userGroups = await userGroupService.fetch();
      context.commit("fetchUserGroups", userGroups);
    },
    deleteUserGroup: async (context, payload) => {
      let {
        component,
        UserGroupId
      } = payload;
      let userGroupService = new UserGroupService(component);
      await userGroupService.delete(UserGroupId);
      let userGroups = await userGroupService.fetch();
      context.commit("fetchUserGroups", userGroups);
    },
    addPage: async (context, payload) => {
      let {
        component,
        UserGroupId,
        PageId
      } = payload;
      let userGroupService = new UserGroupService(component);
      await userGroupService.addPage(UserGroupId, PageId);
      let userGroups = await userGroupService.fetch();
      context.commit("fetchUserGroups", userGroups);
    },
    removePage: async (context, payload) => {
      let {
        component,
        UserGroupId,
        PageId
      } = payload;
      let userGroupService = new UserGroupService(component);
      await userGroupService.removePage(UserGroupId, PageId);
      let userGroups = await userGroupService.fetch();
      context.commit("fetchUserGroups", userGroups);
    },
    SOCKET_USERGROUP_CREATE: async context => {
      let userGroupService = new UserGroupService();
      let userGroups = await userGroupService.fetch();
      context.commit("fetchUserGroups", userGroups);
    },
    SOCKET_USERGROUP_REMOVE: async context => {
      let userGroupService = new UserGroupService();
      let userGroups = await userGroupService.fetch();
      context.commit("fetchUserGroups", userGroups);
    },
    SOCKET_USERGROUP_UPDATE: async context => {
      let userGroupService = new UserGroupService();
      let userGroups = await userGroupService.fetch();
      context.commit("fetchUserGroups", userGroups);
    }

  }
};

export default userGroupModule;